.tyk-table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%; }
  .tyk-table th {
    color: #5a5a68;
    font-size: 12px;
    text-align: left;
    text-transform: uppercase; }
    .tyk-table th .tyk-button {
      background: none;
      color: #5a5a68;
      padding: 0; }
  .tyk-table td, .tyk-table th {
    padding: 10px; }
  .tyk-table tbody tr:nth-child(2n+1) {
    background-color: #F0F0F0; }
  .tyk-table tr td:first-child,
  .tyk-table tr th:first-child {
    padding-left: 25px; }
  .tyk-table tr td:last-child,
  .tyk-table tr th:last-child {
    padding-right: 25px; }
  .tyk-table .tyk-pagination {
    padding: 20px 25px; }
    .tyk-table .tyk-pagination li:first-child {
      margin-left: auto; }

.tyk-table-search {
  padding: 10px 25px 0 25px; }
